body {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
}

#settings {
    float: left;
    padding: 1em;
}
#settings .control-label {
    text-align: left;
}
#settings .form-group {
    margin-bottom: .25em;
}
#settings .form-control[type="number"] {
    padding: 2px;
    height: 2em;
}

#statistics {
    float: right;
    padding: 1em;
}
#statistics .container-fluid {
    white-space: nowrap;
    min-width: 17em;
}

#equations {
/*    
    width: 100%;
    text-align: center;
    padding-top: 10%;
*/
    text-align: center;
    padding-top: 10%;
    justify-content: center;
    align-items: center;
    margin-left: 30%;
    margin-right: 30%;
}
#equations .current {
    font-size: 3em;
    display: inline-block;
}
#equations .current input[type="number"] {
    display: inline-block;
    width: 3em;
}
#equations .current button {
    color: #333;
    background-color: #fff;
    border: 1px solid #8c8c8c;
    padding: 2px 12px;
    border-left: none;
}
#equations .current button:hover {
    background-color: #d4d4d4;
}
#equations .current button:active {
    background-color: #bebebe;
}

.header {
    font-size: 1.5em;
    border-bottom: 1px solid #ebebeb;
    margin: .5em 0;
    text-align: center;
    width: 100%;
}

input[type="number"] {
    text-align: right;
}


